
import moment from 'moment'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default{
directives: {
TransferDom
},
props: {
},
data () {
return {
modalShow: false,
currentYear: 2017,
currentValue: '',
}
},
mounted () {
this.$refs['month'].addEventListener('scroll', this.handleScroll)
},
beforeDestroy () {
this.$refs['month'].removeEventListener('scroll', this.handleScroll)
},
watch: {
modalShow: function (val) {
if(val) {
let month = this.currentValue


let months = parseInt(moment(new Date()).add(1,'y').format('YYYY') - String(month).substring(0,4)) * 12 - parseInt(String(month).substring(4,6)),
height = months*60 + (parseInt(months/12)*20)




this.$nextTick(()=>{
this.$refs['month'].scrollTop = height
})
}
}
},
methods: {
handleScroll () {
let scrollTop = document.getElementsByClassName('month-picker-body')[0].scrollTop
let offsetTop = document.getElementsByClassName('month-picker-body')[0].offsetTop
let itemHeight = document.getElementsByClassName('month-picker-body')[0].children[1].clientHeight
let index = parseInt(scrollTop / (itemHeight - 20))



this.currentYear = this.data[index].year
},
show (month) {
this.currentValue = String(month).substring(0,6)

this.modalShow = true
},
hide () {
this.modalShow = false
},
onItemChange (year,month) {
this.currentYear = year
this.currentValue = year+''+month
this.$emit('on-input',this.currentValue)
this.$emit('on-change',this.currentValue)
this.hide()
}
},
computed: {
currentMonth () {
let currentValue = this.currentValue
return parseInt(currentValue.substring(4,currentValue))
},
data () {
let year = parseInt(moment(new Date()).format('YYYY')),
result = []


for(let i=0;i<11;i++){
let item = {
year: parseInt(year) - i,
months: []
}

for(let j=12;j>0;j--){
item.months.push(j)
}
result.push(item)
}

return result
}
}
}



